  <div class="main">
    <h2 class="conTitle">{{title}}</h2>
    <ul class="aboutNav">
      <li><a href="#aboutIntro">自我介绍</a></li>
      <li><a href="#aboutSkill">专业技能</a></li>
      <li><a href="#aboutWork">工作经历</a></li>
      <li><a href="#aboutProject">我的项目</a></li>
      <li><a href="#aboutTime">时光机</a></li>
      <li><a href="#aboutContact">联系我</a></li>
    </ul>

    <div class="aboutCon">
      <h3 id="aboutIntro" class="abNavItem">自我介绍</h3>
      <p>我叫小桃酥的曲奇饼(无奈...有点长)，直接叫我曲奇也行，92年出生于江苏盐城。现在北京工作，住在霍营那一带。</p>
      <ul>
        <li>喜欢鼓捣各种东西(电子产品、软件系统、博客论坛)，一个动手能力较强的前端职业者。</li>
        <li>乐于助人，不管是平时生活还是工作都喜欢帮助他人</li>
        <li>享受那种问题(bug)在自己手上解决掉的感觉</li>
        <li>有代码强迫症，最初自己写的代码没技术含量(实在看不下去)，慢慢养成了写完一个功能都会回头去梳理逻辑的习惯</li>
      </ul>
      <h3 id="aboutSkill" class="abNavItem">专业技能</h3>
      <ul>
        <li>熟练掌握html+css，能够手写符合w3c标准的页面代码，快速精准还原设计稿，并处理各浏览器兼容问题；</li>
        <li>熟练掌握JavaScript/JQ/BootStrap，完成基于DOM的特效交互，实现功能组件封装；</li>
        <li>熟练掌握SASS\SCSS预处理器，并能够利用IDE进行css自编译</li>
        <li>熟练掌握requireJS，进行模块化开发；</li>
        <li>熟练掌握Ajax/JSONP技术进行异步操作，完成与后台的数据交互；</li>
        <li>熟练HTML5和CSS3技术，并能够基于Hybrid APP开发App内嵌页面；</li>
        <li>熟练能够利用vue-cli进行项目搭建(本博客)，并使用vue+vuex+vue-router进行项目开发</li>
        <li>熟练使用React 、angularJS、senchaTouch等框架</li>
        <li>熟练使用git、github、gulp、webpack、Node；</li>
        <li>开发工具：WebStorm、Sublime Text、node、Markdown、Chrome、Photoshop。</li>
      </ul>
      <h3 id="aboutWork" class="abNavItem">工作经历</h3>
      <ul>
        <li>北京中科视维文化科技有限公司&emsp;<span>|</span>&emsp;2017/10~至今&emsp;&emsp;&emsp;<span>|</span>&emsp;前端工程师</li>
        <li>明博教育科技股份有限公司&emsp;&emsp;&emsp;<span>|</span>&emsp;2015/10~2017/09&emsp;<span>|</span>&emsp;前端工程师</li>
        <li>科翔软通信息技术南通有限公司&emsp;<span>|</span>&emsp;2014/05~2015/08&emsp;<span>|</span>&emsp;网页制作</li>
      </ul>
      <h3 id="aboutProject" class="abNavItem">我的项目</h3>
      <ul>
        <li><a href="javascript:;">光团乐园管理系统(内部项目)</a></li>
        <li><a target="_blank" href="http://www.iotgw.com:8888/">校园物联网智能管理系统</a></li>
        <li><a target="_blank" href="http://yun.myuclass.com//dist/platform/www/home/index.html">优课智慧教育平台</a></li>
        <li><a target="_blank" href="http://ar.myuclass.com/">AR梦幻课堂</a></li>
      </ul>
      <h3 id="aboutTime" class="abNavItem">时光机</h3>
      <p>博客已运行：{{blogTime}}</p>
      <h3 id="aboutContact" class="abNavItem">联系我</h3>
      <ul>
        <li>Email：caiyl@ntsclong.com&emsp;或者&emsp;ntsclong@foxmail.com</li>
        <li>GitHub：github.com/ntsclong</li>
        <li>Gitee：gitee.com/ntsclong</li>
        <li>博客园：www.cnblogs.com/cookies-cnbogs</li>
        <li>QQ：602165066&nbsp;(请注明来意)</li>
      </ul>
    </div>

    <!--<el-button type="primary">主要按钮</el-button>-->
  </div>
</template>

<script>
  export default {
    name: 'about',
    data() {
      return {
        title: '关于',
        blogCreateTime:this.$store.state.blogInfo.createTime,
        blogTime:'',
        timeInterval:function () {}
      }
    },
    components: {},
    mounted: function () {
      window.document.title = '关于 - 小桃酥的曲奇饼';
      let _that= this;
      this.timeInterval=setInterval(function () {
        let newTime = new Date(), nowTime = new Date(_that.blogCreateTime), day, hour, minute, second;
        let spanTimer = newTime - nowTime;
        day = Math.floor(spanTimer / 86400000);
        spanTimer -= day * 86400000;
        hour = Math.floor(spanTimer / 3600000);
        spanTimer -= hour * 3600000;
        minute = Math.floor(spanTimer / 60000);
        spanTimer -= minute * 60000;
        second = Math.floor(spanTimer / 1000);
        _that.blogTime = day + '天' + hour + '时' + minute + '分' + second + '秒'
      }, 1000)
    },
    beforeDestroy:function () {
      window.clearInterval(this.timeInterval)
    }
  }
</script>
<style lang="scss">
  .main {
    margin-bottom: 50px;
    .conTitle {
      font-size: 32px;
      margin-bottom: 25px;
      font-weight: 300;
    }
    .aboutNav {
      margin-left: 20px;
      li {
        list-style: disc;
        margin: 10px 0;
        a {
          color: #0088cc;
        }

      }
    }
    .aboutCon {
      .abNavItem {
        font-size: 24px;
        color: #666;
        font-weight: bold;
        margin: 20px 0;
      }
      p{
        margin: 15px 0;
      }
      ul{
        max-width: 1035px;
        margin-left: 20px;
        li{
          list-style: disc;
          margin: 5px 0;
          span{
            font-weight: bold;
          }
          a{
            color: #0088cc;
          }
        }
      }

    }

  }

</style>



